body {
  min-height: 100vh;
  background: url("../img/luxfon.com-11240.jpg");
  background-size: cover;
  min-width: 320px;
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.01em;
  color: #000000; }

a {
  text-decoration: none;
  color: inherit; }

::placeholder {
  color: #666666; }

::selection {
  background-color: #a7a7a7;
  color: #ffffff; }

input:focus:required:invalid,
textarea:focus:required:invalid {
  border-color: red; }

input:required:valid,
textarea:required:valid {
  border-color: green; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
canvas,
figure,
figcaption,
audio,
video {
  margin: 0;
  padding: 0; }

.svg-icon {
  fill: currentColor; }

.header__inner {
  width: 100%;
  padding: 0 30px; }
  @media (min-width: 768px) {
    .header__inner {
      max-width: 768px;
      padding: 0 50px; } }
  @media (min-width: 1440px) {
    .header__inner {
      max-width: 1440px;
      margin: 0 auto;
      padding: 0 100px; } }

.main__inner {
  width: 100%;
  padding: 0 30px; }
  @media (min-width: 768px) {
    .main__inner {
      max-width: 768px;
      padding: 0 50px; } }
  @media (min-width: 1440px) {
    .main__inner {
      max-width: 1440px;
      margin: 0 auto;
      padding: 0 100px; } }

.container {
  width: 500px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.3);
  min-height: 100vh;
  overflow: hidden; }

.treadle {
  width: 200px;
  height: 350px;
  border: 3px solid black;
  border-radius: 24px;
  margin: 50px auto;
  transform: perspective(205px) rotateX(0deg);
  transition: all ease .5s;
  background: url("../img/migrate.jpg");
  background-size: cover;
  cursor: pointer;
  box-shadow: 2px 5px 5px #000; }

.treadle-push {
  transform: perspective(205px) rotateX(14deg);
  transition: all ease .5s;
  box-shadow: 5px 5px 10px #000; }

.start {
  cursor: pointer;
  width: 100px;
  height: 100px;
  line-height: 100px;
  background: #000;
  color: red;
  border-radius: 50%;
  margin: 50px auto;
  text-align: center;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 24px; }

.progress-wrap {
  border: 5px solid #333;
  margin: 20px auto;
  min-height: 100px;
  max-width: 90%; }

.progress-line {
  height: 100px;
  background: green;
  width: 0;
  transition: all ease 1s; }

.footer__inner {
  width: 100%;
  padding: 0 30px; }
  @media (min-width: 768px) {
    .footer__inner {
      max-width: 768px;
      padding: 0 50px; } }
  @media (min-width: 1440px) {
    .footer__inner {
      max-width: 1440px;
      margin: 0 auto;
      padding: 0 100px; } }
